import {
  Box,
  ClickAwayListener,
  Fade,
  List,
  ListItemButton,
  ListItemText,
  Popper,
  Typography,
} from "@mui/material";

import { Tik, WorkSpace } from "Assets/Icons";
import { useAppState } from "Contexts/App";
import { useEffect } from "react";
import {
  WORKSPACE,
  WORKSPACE_COLOR,
  WORKSPACE_LABEL,
} from "Shared/Enums/WorkSpace";

import {
  SwitchLabel,
  WorkSpaceSwitcherSx,
  WorkSpaceSwitchLabelSx,
  WorkSpaceButtonSx,
  WorkSpaceTextSx,
  WorkSpaceListSx,
} from "./LayoutComponents";

type WorkSpaceSwitcherProps = {
  open: boolean;
  anchorEl: null | HTMLElement;
  doClose: () => void;
};

type WorkSpaceType = {
  wS: WORKSPACE;
  wSLabel: WORKSPACE_LABEL;
  wSColor: WORKSPACE_COLOR;
  wSDesc: string;
};

const WorkSpaceSwitcher = ({
  open,
  anchorEl,
  doClose,
}: WorkSpaceSwitcherProps) => {
  const { workSpace, changeWorkSpace } = useAppState();
  const WorkSpaces: WorkSpaceType[] = [
    {
      wS: WORKSPACE.ENGAGE,
      wSLabel: WORKSPACE_LABEL.ENGAGE,
      wSColor: WORKSPACE_COLOR.ENGAGE,
      wSDesc: "Manage & engage in conversations with whatsapp chat bot.",
    },
    {
      wS: WORKSPACE.CURATE,
      wSLabel: WORKSPACE_LABEL.CURATE,
      wSColor: WORKSPACE_COLOR.CURATE,
      wSDesc: "All social media in one place -  Instagram, Facebook & more.",
    },
  ];

  useEffect(() => {
    doClose();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [workSpace]);

  return (
    <Popper
      open={open}
      anchorEl={anchorEl}
      placement="bottom-end"
      sx={WorkSpaceSwitcherSx}
      transition
    >
      {({ TransitionProps }) => (
        <Fade {...TransitionProps} timeout={350}>
          <Box className="wrapper">
            <ClickAwayListener onClickAway={doClose}>
              <Box className="inner">
                <Typography component="h6" sx={WorkSpaceSwitchLabelSx}>
                  Choose Workspace
                </Typography>
                <List sx={WorkSpaceListSx}>
                  {WorkSpaces.map(
                    ({ wS, wSLabel, wSColor, wSDesc }, index: number) => (
                      <ListItemButton
                        key={index}
                        className={workSpace === wS ? "active" : ""}
                        sx={WorkSpaceButtonSx}
                        onClick={() => changeWorkSpace(wS)}
                      >
                        <ListItemText
                          primary={
                            <Box
                              alignItems="center"
                              color={wSColor}
                              display="flex"
                              justifyContent="space-between"
                              gap={1}
                            >
                              <WorkSpace />
                              <SwitchLabel sx={{ flexGrow: 1 }}>
                                {wSLabel}
                              </SwitchLabel>
                              {workSpace === wS && <Tik />}
                            </Box>
                          }
                          secondary={wSDesc}
                          sx={WorkSpaceTextSx}
                        />
                      </ListItemButton>
                    )
                  )}
                </List>
              </Box>
            </ClickAwayListener>
          </Box>
        </Fade>
      )}
    </Popper>
  );
};

export default WorkSpaceSwitcher;
