import {
  Fragment,
  MouseEvent,
  ReactElement,
  SyntheticEvent,
  useEffect,
  useState,
} from "react";
import {
  Fade,
  IconButton,
  Popover,
  Tab,
  Tabs,
  Typography,
} from "@mui/material";

import { useGlobal } from "Contexts/App";
import {
  Home,
  Calendar,
  Chat,
  Users,
  EasySocial,
  EasySocialFull,
  Dashboard,
  Broadcast,
} from "Assets/Icons";
import { AppNavLink, ImageBox, ToolTippedTab } from "Components/Common";

import {
  BrandSx,
  getSidebarTabsSx,
  SidebarWrapper,
  SidebarWrapperSx,
  SideLogoPopLabelSx,
  SideLogoPopoverSx,
  SideLogoSx,
} from "./LayoutComponents";
import BusinessSwitcher from "./BusinessSwitcher";

type TabletMenu = {
  title: string;
  to: string;
  icon: ReactElement;
};

type MobileMenu = {
  label: string;
  to: string;
  icon: ReactElement;
};

const Sidebar = () => {
  const { appVer, isTablet, isMobile } = useGlobal();
  const [verEl, setVerEl] = useState<HTMLButtonElement | null>(null);
  const [businessSwitcherEl, setBusinessSwitcherEl] =
    useState<null | HTMLElement>(null);
  const [tabIndex, setTabIndex] = useState<number>(0);
  const showVer = Boolean(verEl);
  const displayVer = (event: React.MouseEvent<HTMLButtonElement>) =>
    setVerEl(event.currentTarget);
  const closeVer = () => setVerEl(null);
  const handleChange = (_event: SyntheticEvent, newIndex: number) =>
    setTabIndex(newIndex);
  const TabProps = ({ children, ...tabProps }: { children: any }) =>
    children(tabProps);
  const sideMenuTablet: TabletMenu[] = [
    { title: "Home", to: "/engage", icon: <Home /> },
    { title: "Dashboard", to: "/engage", icon: <Dashboard /> },
    { title: "Chat", to: "/engage", icon: <Chat /> },
    { title: "Broadcast", to: "/engage", icon: <Broadcast /> },
    { title: "Schedule", to: "/engage", icon: <Calendar /> },
    { title: "Members", to: "/engage", icon: <Users /> },
  ];
  const sideMenuMobile: MobileMenu[] = [
    { label: "Chat", to: "/engage", icon: <Chat /> },
    { label: "Broadcast", to: "/engage", icon: <Broadcast /> },
  ];

  // ? added this cause toggling into mobile from desktop when
  // ? greater than 1 there was nothing active on mobile
  // ? ↓ We can remove this later if needed ↓
  useEffect(() => {
    setTabIndex(0);
  }, [isMobile]);

  return (
    <Fragment>
      <SidebarWrapper sx={SidebarWrapperSx}>
        {isTablet && (
          <IconButton
            sx={BrandSx}
            onClick={(event: MouseEvent<HTMLElement>) => {
              setBusinessSwitcherEl(
                businessSwitcherEl ? null : event.currentTarget
              );
            }}
          >
            <ImageBox
              src="/static/images/jpg/honda-logo.jpg"
              alt="brand-image"
            />
          </IconButton>
        )}
        <Tabs
          orientation={isMobile ? "horizontal" : "vertical"}
          variant={isMobile ? "fullWidth" : "scrollable"}
          value={tabIndex}
          onChange={handleChange}
          sx={getSidebarTabsSx(isMobile)}
        >
          {isTablet &&
            sideMenuTablet.map(
              ({ title, to, icon }: TabletMenu, index: number) => (
                <TabProps key={index}>
                  {(tabProps: any) => (
                    <ToolTippedTab key={index} title={title}>
                      <Tab
                        {...tabProps}
                        component={AppNavLink}
                        key={index}
                        to={to}
                        icon={icon}
                        sx={{ minWidth: 60, minHeight: 60 }}
                      />
                    </ToolTippedTab>
                  )}
                </TabProps>
              )
            )}
          {isMobile &&
            sideMenuMobile.map(
              ({ label, to, icon }: MobileMenu, index: number) => (
                <Tab
                  key={index}
                  component={AppNavLink}
                  to={to}
                  label={label}
                  icon={icon}
                  sx={{ minWidth: 60, minHeight: 60 }}
                  textColor="secondary"
                />
              )
            )}
        </Tabs>
        {isTablet && (
          <Fragment>
            <IconButton sx={SideLogoSx} onClick={displayVer}>
              <EasySocial />
            </IconButton>
            <Popover
              open={showVer}
              anchorEl={verEl}
              onClose={closeVer}
              anchorOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              TransitionComponent={Fade}
              TransitionProps={{ timeout: 400 }}
              sx={SideLogoPopoverSx}
            >
              <EasySocialFull />
              <Typography
                component="span"
                sx={SideLogoPopLabelSx}
              >{`Version ${appVer}`}</Typography>
            </Popover>
          </Fragment>
        )}
      </SidebarWrapper>
      <BusinessSwitcher
        open={Boolean(businessSwitcherEl)}
        anchorEl={businessSwitcherEl}
        doClose={() => setBusinessSwitcherEl(null)}
      />
    </Fragment>
  );
};

export default Sidebar;
