import { useState } from "react";
import {
  Box,
  ClickAwayListener,
  Drawer,
  Fade,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Paper,
} from "@mui/material";
import { Check } from "@mui/icons-material";

import { useGlobal } from "Contexts/App";
import { ListItemImage } from "Components/Common";
import { TipArrowLeft } from "Assets/Icons";

import {
  ArrowPaperSx,
  ArrowPopper,
  ArrowSx,
  BusinessButtonSx,
  BusinessNameSx,
  BusinessPopperButtonSx,
  BusinessSwitchDrawerSx,
  BusinessSwitchLabel,
  BusinessSwitchWrapper,
} from "./LayoutComponents";

type BusinessSwitcherProps = {
  open: boolean;
  anchorEl?: null | HTMLElement;
  doClose: () => void;
};

const BusinessSwitcher = ({
  open,
  anchorEl,
  doClose,
}: BusinessSwitcherProps) => {
  const { isMobile, isTablet } = useGlobal();
  const [arrowRef, setArrowRef] = useState<HTMLElement | null>(null);

  return isMobile ? (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={doClose}
      sx={BusinessSwitchDrawerSx}
    >
      <BusinessSwitchWrapper>
        <BusinessSwitchLabel label="Select Business" />
        <List sx={{ px: 1 }}>
          {Array(5)
            .fill(undefined)
            .map((_: undefined, i: number, arr: undefined[]) => (
              <ListItemButton
                key={i}
                onClick={doClose}
                sx={BusinessButtonSx(arr.length, i)}
              >
                <ListItemImage
                  src={"/static/images/jpg/honda-logo.jpg"}
                  alt={`Brand ${i + 1}`}
                />
                <ListItemText primary="Honda 2 Wheelers" sx={BusinessNameSx} />
                {i === 0 && (
                  <ListItemIcon>
                    <Check sx={{ color: "#12B0EE" }} />
                  </ListItemIcon>
                )}
              </ListItemButton>
            ))}
        </List>
      </BusinessSwitchWrapper>
    </Drawer>
  ) : isTablet ? (
    <ArrowPopper
      placement="right-start"
      open={open}
      anchorEl={anchorEl}
      modifiers={[
        {
          name: "arrow",
          enabled: true,
          options: {
            element: arrowRef,
          },
        },
      ]}
      transition
    >
      {({ TransitionProps }) => (
        <Fade {...TransitionProps} in={open} timeout={350}>
          <Box marginLeft={1.125} position="relative">
            <Box component="span" ref={setArrowRef} sx={ArrowSx}>
              <TipArrowLeft />
            </Box>
            <ClickAwayListener onClickAway={doClose}>
              <Paper sx={ArrowPaperSx}>
                <BusinessSwitchLabel label="Select Business" />
                <List sx={{ px: 1 }}>
                  {Array(5)
                    .fill(undefined)
                    .map((_: undefined, i: number, arr: undefined[]) => (
                      <ListItemButton
                        key={i}
                        onClick={doClose}
                        sx={BusinessPopperButtonSx(arr.length, i)}
                      >
                        <ListItemImage
                          src={"/static/images/jpg/honda-logo.jpg"}
                          alt={`Brand ${i + 1}`}
                        />
                        <ListItemText
                          primary="Honda 2 Wheelers"
                          sx={BusinessNameSx}
                        />
                        {i === 0 && (
                          <ListItemIcon>
                            <Check sx={{ color: "#12B0EE" }} />
                          </ListItemIcon>
                        )}
                      </ListItemButton>
                    ))}
                </List>
              </Paper>
            </ClickAwayListener>
          </Box>
        </Fade>
      )}
    </ArrowPopper>
  ) : null;
};

export default BusinessSwitcher;
