import {
  Box,
  Button,
  Drawer,
  List,
  Menu,
  MenuItem,
  Typography,
} from "@mui/material";

import {
  Dismiss,
  EasySocialFullLarge,
  UserBilling,
  UserLogout,
  UserProfile,
  UserSettings,
} from "Assets/Icons";
import { useGlobal } from "Contexts/App";
import { useNavigate } from "react-router-dom";

import {
  AccountMenuDrawerSX,
  AccountMenuListSX,
  AccountMenuSX,
  AppVerSx,
  MobileMenuIconSx,
} from "./LayoutComponents";

type AccountMenuProps = {
  open: boolean;
  anchorEl?: null | HTMLElement;
  doClose: () => void;
};

const AccountMenu = ({ open, anchorEl, doClose }: AccountMenuProps) => {
  const navigate = useNavigate();
  const { appVer, isMobile, isTablet } = useGlobal();

  return isMobile ? (
    <Drawer
      anchor={"left"}
      open={open}
      onClose={doClose}
      sx={AccountMenuDrawerSX}
    >
      <Box display="flex" flexDirection="row-reverse">
        <Button
          variant="text"
          color="inherit"
          onClick={doClose}
          disableRipple={true}
          disableFocusRipple={true}
          disableTouchRipple={true}
          sx={{ color: "#66686C", minWidth: "unset" }}
        >
          <Dismiss />
        </Button>
      </Box>
      <List sx={AccountMenuListSX}>
        <MenuItem onClick={doClose}>
          <UserProfile sx={MobileMenuIconSx} />
          {"My Profile"}
        </MenuItem>
        <MenuItem
          onClick={() => {
            navigate("/business/general-settings");
            doClose();
          }}
        >
          <UserSettings sx={MobileMenuIconSx} />
          {"Settings"}
        </MenuItem>
        <MenuItem onClick={doClose}>
          <UserBilling sx={MobileMenuIconSx} />
          {"Billing"}
        </MenuItem>
        <MenuItem onClick={doClose}>
          <UserLogout sx={MobileMenuIconSx} />
          {"Log Out"}
        </MenuItem>
      </List>
      <Box display="flex" justifyContent="center" mb={1.75}>
        <Box display="flex" flexDirection="column" alignItems="end">
          <EasySocialFullLarge />
          <Typography
            component="span"
            sx={AppVerSx}
          >{`Version ${appVer}`}</Typography>
        </Box>
      </Box>
    </Drawer>
  ) : isTablet ? (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={doClose}
      onClick={doClose}
      keepMounted
      MenuListProps={{
        "aria-labelledby": "Account Menu",
      }}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      sx={AccountMenuSX}
    >
      <MenuItem>
        <UserProfile />
        {"My Profile"}
      </MenuItem>
      <MenuItem onClick={() => navigate("/business/general-settings")}>
        <UserSettings />
        {"Settings"}
      </MenuItem>
      <MenuItem>
        <UserBilling />
        {"Billing"}
      </MenuItem>
      <MenuItem>
        <UserLogout />
        {"Log Out"}
      </MenuItem>
    </Menu>
  ) : null;
};

export default AccountMenu;
